<template>
    <div>
      <nav-header></nav-header>
      <nav-bread>
        <span>Goods</span>
      </nav-bread>
      <div class="accessory-result-page accessory-page">
        <div class="container">
          <div class="filter-nav">
            <span class="sortby">Sort by:</span>
            <a href="javascript:void(0)" class="default cur">Default</a>
            <a href="javascript:void(0)" class="price" @click="sortGoods">
              Price
              <svg class="icon icon-arrow-short" v-bind:class = "{'sort-up':!sortFlag}">
                <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#icon-arrow-short"></use>
              </svg>
            </a>
            <a href="javascript:void(0)" class="filterby stopPop" @click = "showFilterPop">Filter by</a>
          </div>
          <div class="accessory-result">
            <!-- filter -->
            <div class="filter stopPop" id="filter" v-bind:class="{'filterby-show':filterBy}">
              <dl class="filter-price">
                <dt>Price:</dt>
                <dd><a href="javascript:void(0)" v-bind:class="{'cur': priceChecked === 'all'}" @click="priceChecked = 'all'">All</a></dd>
                <dd v-for="(price, index) in priceFilter">
                  <a href="javascript:void(0)" v-bind:class="{'cur':priceChecked === index}" @click = "setPriceFilter(index)">{{price.startPrice}} - {{price.endPrice}}</a>
                </dd>
              </dl>
            </div>

            <!-- search result accessories list -->
            <div class="accessory-list-wrap">
              <div class="accessory-list col-4">
                <ul>
                  <li v-for = "(item, index) in goodsList">
                    <div class="pic">
                      <a href="#"><img v-lazy="'/static/' + item.productImage" alt=""></a>
                    </div>
                    <div class="main">
                      <div class="name">{{item.productName}}</div>
                      <div class="price">{{item.salePrice}}</div>
                      <div class="btn-area">
                        <a href="javascript:;" class="btn btn--m" @click="addCart(item.productId)"></a>
                      </div>
                    </div>
                  </li>
                </ul>
                <div class="load-more" v-infinite-scroll="loadMore" infinite-scroll-disabled="busy" infinite-scroll-distance="30">
                  <img src="../assets/loading-spinning-bubbles.svg" alt="" v-show = 'loading'>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="md-overlay" v-show="overLayFlag" @click="closePop"></div>
      <modal v-bind:mdShow="mdShow" v-on:close="closeModal">
          <p slot="message">
              
          </p>
        <div slot="btnGroup">
          <a class = "btn btn--m" @click="mdShow =false"></a>
        </div>
      </modal>
      <modal v-bind:mdShow="mdShowCart" v-on:close="closeCartModal">
        <p slot="message">
          <svg class="icon-status-ok" v-bind:class = "{'sort-up':!sortFlag}">
            <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#icon-status-ok"></use>
          </svg>
          <span></span>
        </p>
        <div slot="btnGroup">
          <a class = "btn btn--m" @click="mdShowCart =false"></a>
          <router-link class="btn btn--m" to="/cart"></router-link>
        </div>
      </modal>
      <nav-footer></nav-footer>
    </div>
</template>
<style scoped>
  .sort-up{
    transform: rotate(180deg);
    transition: all .3s ease-out;
  }
</style>

<script>
  import '../assets/css/base.css'
  import '../assets/css/login.css'
  import '../assets/css/product.css'
  import NavHeader from '@/components/Header.vue'
  import NavFooter from '@/components/Footer.vue'
  import NavBread from '@/components/Bread'
  import Modal from '@/components/Modal'
  import axios from 'axios'
    export default {
        data() {
            return {
                goodsList: [],
                priceFilter:[
                  {
                    startPrice:'0.00',
                    endPrice:'100.00'
                  },
                  {
                    startPrice:'100.00',
                    endPrice:'500.00'
                  },
                  {
                    startPrice:'500.00',
                    endPrice:'1000.00'
                  },
                  {
                    startPrice:'1000.00',
                    endPrice:'2000.00'
                  }
                ],
                priceChecked: 'all',
                filterBy:false,
                overLayFlag:false,
                sortFlag:true,
                page:1,
                pageSize:8,
                busy:true,
                loading:false,
                mdShow:false,
                mdShowCart:false
            }
        },
      components:{
          NavHeader,
          NavFooter,
          NavBread,
          Modal
      },
      mounted:function(){
        this.getGoodsList()
      },
      methods:{
        getGoodsList(flag){
          let params = {
            page:this.page,
            pageSize:this.pageSize,
            sort:this.sortFlag ? 1 : -1,
            priceLevel:this.priceChecked
          }
          this.loading = true
          axios.get('/goods/list',{params}).then((result)=>{
            this.loading = false
            let res =result.data
            if(res.status == "0"){
              if(flag){
                this.goodsList = this.goodsList.concat(res.result.list);
                if(res.result.count == 0){
                  this.busy = true
                }else{
                  this.busy = false
                }
              }else{
                this.goodsList = res.result.list;
                this.busy = false
              }
            }else{
              this.goodsList = []
            }
          })
        },
        showFilterPop(){
          this.filterBy = true
          this.overLayFlag = true
        },
        closePop(){
          this.filterBy = false
          this.overLayFlag = false
        },
        setPriceFilter(index){
          this.priceChecked = index
          this.page = 1
          this.getGoodsList()
          this.closePop()
        },
        sortGoods(){
          this.sortFlag = !this.sortFlag
          this.page = 1
          this.getGoodsList()
        },
        loadMore(){
          this.busy = true
          setTimeout(() => {
            this.page ++
            this.getGoodsList(true)
          }, 500);
        },
        addCart(productId){
          axios.post('/goods/addCart',{productId:productId}).then((res)=>{
            if(res.data.status == 0){
              this.mdShowCart = true
              this.$store.commit('updateCartCount',1)
            }else{
              //alert('msg:' + res.data.msg)
              this.mdShow = true
            }
          })
        },
        closeModal(){
          this.mdShow = false
        },
        closeCartModal(){
          this.mdShowCart = false
        }
      }
    }
</script>




